import React, { useState, useEffect, useMemo } from 'react';
import DataTable from '../components/DataTable';
import KPICard from '../components/KPICard';
import { sellerApi, asinApi } from '../services/api';

const SellersPage = () => {
  const [sellers, setSellers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [showAddModal, setShowAddModal] = useState(false);
  const [showImportModal, setShowImportModal] = useState(false);
  const [showAsinModal, setShowAsinModal] = useState(false);
  const [selectedSeller, setSelectedSeller] = useState(null);
  const [sellerAsins, setSellerAsins] = useState([]);
  const [activeTab, setActiveTab] = useState('all');
  const [seeding, setSeeding] = useState(false);

  useEffect(() => {
    loadSellers();
  }, []);

  const loadSellers = async () => {
    setLoading(true);
    try {
      const response = await sellerApi.getAll();
      setSellers(response.sellers);
    } catch (error) {
      console.error('Failed to load sellers:', error);
    }
    setLoading(false);
  };

  const filteredSellers = useMemo(() => {
    if (activeTab === 'all') return sellers;
    return sellers.filter(s => s.status.toLowerCase() === activeTab);
  }, [sellers, activeTab]);

  const kpis = useMemo(() => [
    { title: 'Total Sellers', value: sellers.length.toString(), icon: 'bi-shop', trend: sellers.length, trendType: 'neutral' },
    { title: 'Active', value: sellers.filter(s => s.status === 'Active').length.toString(), icon: 'bi-check-circle', trend: sellers.filter(s => s.status === 'Active').length, trendType: 'positive' },
    { title: 'Paused', value: sellers.filter(s => s.status === 'Paused').length.toString(), icon: 'bi-pause-circle', trend: sellers.filter(s => s.status === 'Paused').length, trendType: 'neutral' },
    { title: 'Total ASINs', value: sellers.reduce((sum, s) => sum + s.totalAsins, 0).toLocaleString(), icon: 'bi-box-seam', trend: sellers.reduce((sum, s) => sum + s.totalAsins, 0), trendType: 'positive' },
  ], [sellers]);

  const handleAddSeller = async (sellerData) => {
    try {
      await sellerApi.create(sellerData);
      await loadSellers();
      setShowAddModal(false);
    } catch (error) {
      alert('Failed to add seller: ' + error.message);
    }
  };

  const handleImportSellers = async (csvData) => {
    try {
      const lines = csvData.split('\n').filter(l => l.trim());
      const newSellers = [];
      lines.forEach((line, idx) => {
        if (idx === 0) return; // Skip header
        const [name, marketplace, sellerId, apiKey] = line.split(',').map(s => s.trim());
        if (name && marketplace && sellerId) {
          newSellers.push({
            name,
            marketplace,
            sellerId,
            apiKey: apiKey || '',
            plan: 'Starter',
            scrapeLimit: 100,
            totalAsins: 0,
            activeAsins: 0,
            status: 'Active',
          });
        }
      });
      
      if (newSellers.length > 0) {
        await sellerApi.import(newSellers);
        await loadSellers();
        setShowImportModal(false);
      }
    } catch (error) {
      alert('Failed to import sellers: ' + error.message);
    }
  };

  const handleToggleStatus = async (sellerId) => {
    try {
      const seller = sellers.find(s => s._id === sellerId);
      if (seller) {
        await sellerApi.update(sellerId, { 
          status: seller.status === 'Active' ? 'Paused' : 'Active' 
        });
        await loadSellers();
      }
    } catch (error) {
      alert('Failed to update seller: ' + error.message);
    }
  };

  const handleDeleteSeller = async (sellerId) => {
    if (window.confirm('Are you sure you want to delete this seller? All ASINs will also be deleted.')) {
      try {
        await sellerApi.delete(sellerId);
        await loadSellers();
      } catch (error) {
        alert('Failed to delete seller: ' + error.message);
      }
    }
  };

  const handleViewAsins = async (seller) => {
    setSelectedSeller(seller);
    setShowAsinModal(true);
    try {
      const asins = await asinApi.getBySeller(seller._id);
      setSellerAsins(asins);
    } catch (error) {
      console.error('Failed to load ASINs:', error);
      setSellerAsins([]);
    }
  };

  const handleAddAsin = async (asinData) => {
    try {
      await asinApi.create({
        ...asinData,
        seller: selectedSeller._id,
        status: 'Active',
      });
      const asins = await asinApi.getBySeller(selectedSeller._id);
      setSellerAsins(asins);
      await loadSellers();
    } catch (error) {
      alert('Failed to add ASIN: ' + error.message);
    }
  };

  const handleDeleteAsin = async (asinId) => {
    try {
      await asinApi.delete(asinId);
      const asins = await asinApi.getBySeller(selectedSeller._id);
      setSellerAsins(asins);
      await loadSellers();
    } catch (error) {
      alert('Failed to delete ASIN: ' + error.message);
    }
  };

  const handleSeedDemo = async () => {
    if (!window.confirm('This will seed demo data. Continue?')) return;
    
    setSeeding(true);
    try {
      const result = await sellerApi.seedDemo();
      alert(`Demo data seeded: ${result.sellers} sellers, ${result.asins} ASINs`);
      await loadSellers();
    } catch (error) {
      console.error('Seed error:', error);
      // If demo data already exists, just reload
      await loadSellers();
    }
    setSeeding(false);
  };

  const getStatusBadge = (status) => {
    return status === 'Active' 
      ? <span className="badge badge-success"><i className="bi bi-check-circle me-1"></i>Active</span>
      : <span className="badge badge-secondary"><i className="bi bi-pause-circle me-1"></i>Paused</span>;
  };

  const getMarketplaceBadge = (marketplace) => {
    const colors = {
      'amazon.in': 'bg-primary-subtle text-primary',
      'amazon.com': 'bg-warning-subtle text-warning',
      'amazon.uk': 'bg-info-subtle text-info',
    };
    return <span className={`badge ${colors[marketplace] || 'bg-secondary'}`}>{marketplace}</span>;
  };

  if (loading) {
    return (
      <>
        <header className="main-header">
          <h1 className="page-title"><i className="bi bi-shop"></i>Seller Management</h1>
        </header>
        <div className="page-content">
          <div className="d-flex justify-content-center align-items-center" style={{ minHeight: '400px' }}>
            <div className="loading-spinner"></div>
          </div>
        </div>
      </>
    );
  }

  return (
    <>
      <header className="main-header">
        <div className="d-flex justify-content-between align-items-center flex-wrap gap-3">
          <h1 className="page-title"><i className="bi bi-shop"></i>Seller Management</h1>
          <div className="d-flex gap-2">
            {sellers.length === 0 && (
              <button className="btn btn-outline-success" onClick={handleSeedDemo} disabled={seeding}>
                <i className="bi bi-database-add me-2"></i>
                {seeding ? 'Seeding...' : 'Seed Demo Data'}
              </button>
            )}
            <button className="btn btn-outline-secondary" onClick={() => setShowImportModal(true)}>
              <i className="bi bi-file-earmark-arrow-up me-2"></i>Import CSV
            </button>
            <button className="btn btn-primary" onClick={() => setShowAddModal(true)}>
              <i className="bi bi-plus-lg me-2"></i>Add Seller
            </button>
          </div>
        </div>
      </header>
      <div className="page-content">
        <div className="kpi-grid mb-4" style={{ gridTemplateColumns: 'repeat(auto-fit, minmax(140px, 1fr))' }}>
          {kpis.map((kpi, idx) => (
            <KPICard key={idx} title={kpi.title} value={kpi.value} icon={kpi.icon} trend={kpi.trend} trendType={kpi.trendType} />
          ))}
        </div>

        {/* Tabs */}
        <div className="card mb-4">
          <div className="card-body p-0">
            <ul className="nav nav-tabs border-bottom">
              {['all', 'active', 'paused'].map(tab => (
                <li className="nav-item" key={tab}>
                  <button 
                    className={`nav-link ${activeTab === tab ? 'active' : ''}`}
                    onClick={() => setActiveTab(tab)}
                    style={{ border: 'none', cursor: 'pointer' }}
                  >
                    {tab.charAt(0).toUpperCase() + tab.slice(1)}
                    {tab !== 'all' && (
                      <span className="ms-2 badge bg-secondary-subtle text-secondary">
                        {sellers.filter(s => s.status.toLowerCase() === tab).length}
                      </span>
                    )}
                  </button>
                </li>
              ))}
            </ul>
          </div>
        </div>

        {/* Sellers Table */}
        <div className="card">
          <div className="card-header d-flex justify-content-between align-items-center">
            <h5 className="mb-0"><i className="bi bi-list"></i> All Sellers ({filteredSellers.length})</h5>
            <div className="d-flex gap-2">
              <input 
                type="text" 
                className="form-control form-control-sm" 
                placeholder="Search sellers..."
                style={{ width: '200px' }}
              />
            </div>
          </div>
          <div className="card-body p-0">
            <div className="table-responsive">
              <table className="table data-table mb-0">
                <thead>
                  <tr>
                    <th>Seller Name</th>
                    <th>Marketplace</th>
                    <th>Seller ID</th>
                    <th>ASINs</th>
                    <th>Scrape Usage</th>
                    <th>Last Scraped</th>
                    <th>Status</th>
                    <th>Actions</th>
                  </tr>
                </thead>
                <tbody>
                  {filteredSellers.length === 0 ? (
                    <tr>
                      <td colSpan="8" className="text-center text-muted py-4">
                        <i className="bi bi-inbox d-block mb-2" style={{ fontSize: '24px' }}></i>
                        No sellers found. Add a seller or seed demo data.
                        {sellers.length === 0 && (
                          <button className="btn btn-outline-success btn-sm mt-2" onClick={handleSeedDemo}>
                            <i className="bi bi-database-add me-1"></i>Seed Demo Data
                          </button>
                        )}
                      </td>
                    </tr>
                  ) : (
                    filteredSellers.map(seller => (
                      <tr key={seller._id}>
                        <td>
                          <div className="d-flex align-items-center gap-2">
                            <div className="seller-avatar bg-primary-subtle text-primary rounded-circle d-flex align-items-center justify-content-center" style={{ width: '36px', height: '36px', fontWeight: '600' }}>
                              {seller.name.charAt(0)}
                            </div>
                            <div>
                              <div className="fw-medium">{seller.name}</div>
                              <div className="fs-xs text-muted">{seller.plan} Plan</div>
                            </div>
                          </div>
                        </td>
                        <td>{getMarketplaceBadge(seller.marketplace)}</td>
                        <td className="font-monospace fs-sm">{seller.sellerId}</td>
                        <td>
                          <button 
                            className="btn btn-sm btn-link p-0"
                            onClick={() => handleViewAsins(seller)}
                          >
                            {seller.totalAsins || 0} total
                          </button>
                          <div className="fs-xs text-muted">{seller.activeAsins || 0} active</div>
                        </td>
                        <td>
                          <div className="d-flex align-items-center gap-2">
                            <div className="progress flex-grow-1" style={{ height: '8px', width: '80px' }}>
                              <div 
                                className={`progress-bar ${(seller.scrapeUsed || 0) / (seller.scrapeLimit || 100) > 0.8 ? 'bg-danger' : 'bg-primary'}`}
                                style={{ width: `${((seller.scrapeUsed || 0) / (seller.scrapeLimit || 100)) * 100}%` }}
                              ></div>
                            </div>
                            <span className="fs-xs text-muted">{(seller.scrapeUsed || 0)}/{seller.scrapeLimit || 100}</span>
                          </div>
                        </td>
                        <td className="fs-sm text-muted">
                          {seller.lastScraped ? new Date(seller.lastScraped).toLocaleString() : 'Never'}
                        </td>
                        <td>{getStatusBadge(seller.status)}</td>
                        <td>
                          <div className="d-flex gap-1">
                            <button 
                              className="btn btn-sm btn-outline-primary" 
                              onClick={() => handleViewAsins(seller)}
                              title="View ASINs"
                            >
                              <i className="bi bi-box-seam"></i>
                            </button>
                            <button 
                              className="btn btn-sm btn-outline-secondary" 
                              onClick={() => handleToggleStatus(seller._id)}
                              title={seller.status === 'Active' ? 'Pause' : 'Activate'}
                            >
                              <i className={`bi ${seller.status === 'Active' ? 'bi-pause' : 'bi-play'}`}></i>
                            </button>
                            <button 
                              className="btn btn-sm btn-outline-danger" 
                              onClick={() => handleDeleteSeller(seller._id)}
                              title="Delete"
                            >
                              <i className="bi bi-trash"></i>
                            </button>
                          </div>
                        </td>
                      </tr>
                    ))
                  )}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>

      {/* Add Seller Modal */}
      {showAddModal && (
        <AddSellerModal 
          onClose={() => setShowAddModal(false)} 
          onSave={handleAddSeller} 
        />
      )}

      {/* Import Modal */}
      {showImportModal && (
        <ImportSellerModal 
          onClose={() => setShowImportModal(false)} 
          onImport={handleImportSellers} 
        />
      )}

      {/* ASINs Modal */}
      {showAsinModal && selectedSeller && (
        <SellerAsinsModal 
          seller={selectedSeller}
          asins={sellerAsins}
          onClose={() => { setShowAsinModal(false); setSelectedSeller(null); }}
          onAddAsin={handleAddAsin}
          onDeleteAsin={handleDeleteAsin}
        />
      )}
    </>
  );
};

// Add Seller Modal Component
const AddSellerModal = ({ onClose, onSave }) => {
  const [formData, setFormData] = useState({
    name: '',
    marketplace: 'amazon.in',
    sellerId: '',
    apiKey: '',
    plan: 'Starter',
    scrapeLimit: 100,
  });

  const handleSubmit = (e) => {
    e.preventDefault();
    onSave(formData);
  };

  return (
    <div className="modal show d-block" style={{ backgroundColor: 'rgba(0,0,0,0.5)' }}>
      <div className="modal-dialog">
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title"><i className="bi bi-plus-circle me-2"></i>Add New Seller</h5>
            <button type="button" className="btn-close" onClick={onClose}></button>
          </div>
          <form onSubmit={handleSubmit}>
            <div className="modal-body">
              <div className="mb-3">
                <label className="form-label">Seller Name *</label>
                <input 
                  type="text" 
                  className="form-control" 
                  value={formData.name}
                  onChange={(e) => setFormData({...formData, name: e.target.value})}
                  required
                />
              </div>
              <div className="mb-3">
                <label className="form-label">Marketplace *</label>
                <select 
                  className="form-select"
                  value={formData.marketplace}
                  onChange={(e) => setFormData({...formData, marketplace: e.target.value})}
                >
                  <option value="amazon.in">Amazon India (amazon.in)</option>
                  <option value="amazon.com">Amazon US (amazon.com)</option>
                  <option value="amazon.uk">Amazon UK (amazon.uk)</option>
                  <option value="amazon.de">Amazon Germany (amazon.de)</option>
                </select>
              </div>
              <div className="mb-3">
                <label className="form-label">Seller ID *</label>
                <input 
                  type="text" 
                  className="form-control" 
                  value={formData.sellerId}
                  onChange={(e) => setFormData({...formData, sellerId: e.target.value})}
                  placeholder="A1B2C3D4E5F6"
                  required
                />
              </div>
              <div className="mb-3">
                <label className="form-label">Octoparse API Key</label>
                <input 
                  type="password" 
                  className="form-control" 
                  value={formData.apiKey}
                  onChange={(e) => setFormData({...formData, apiKey: e.target.value})}
                  placeholder="Enter Octoparse API key"
                />
              </div>
              <div className="row">
                <div className="col-md-6 mb-3">
                  <label className="form-label">Plan</label>
                  <select 
                    className="form-select"
                    value={formData.plan}
                    onChange={(e) => setFormData({...formData, plan: e.target.value})}
                  >
                    <option value="Starter">Starter (100/day)</option>
                    <option value="Pro">Pro (500/day)</option>
                    <option value="Enterprise">Enterprise (2000/day)</option>
                  </select>
                </div>
                <div className="col-md-6 mb-3">
                  <label className="form-label">Daily Scrape Limit</label>
                  <input 
                    type="number" 
                    className="form-control" 
                    value={formData.scrapeLimit}
                    onChange={(e) => setFormData({...formData, scrapeLimit: parseInt(e.target.value)})}
                  />
                </div>
              </div>
            </div>
            <div className="modal-footer">
              <button type="button" className="btn btn-secondary" onClick={onClose}>Cancel</button>
              <button type="submit" className="btn btn-primary">
                <i className="bi bi-check-lg me-2"></i>Add Seller
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

// Import Seller Modal Component
const ImportSellerModal = ({ onClose, onImport }) => {
  const [csvData, setCsvData] = useState('');

  const handleImport = () => {
    if (csvData.trim()) {
      onImport(csvData);
    }
  };

  const downloadTemplate = () => {
    const template = `Seller Name,Marketplace,Seller ID,API Key
TechGear Pro,amazon.in,A1B2C3D4E5F6,oct_xxx123
HomeEssentials,amazon.com,A2B3C4D5E6F7,oct_xxx456`;
    const blob = new Blob([template], { type: 'text/csv' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = 'sellers_import_template.csv';
    a.click();
  };

  return (
    <div className="modal show d-block" style={{ backgroundColor: 'rgba(0,0,0,0.5)' }}>
      <div className="modal-dialog">
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title"><i className="bi bi-file-earmark-arrow-up me-2"></i>Import Sellers from CSV</h5>
            <button type="button" className="btn-close" onClick={onClose}></button>
          </div>
          <div className="modal-body">
            <div className="alert alert-info mb-3">
              <i className="bi bi-info-circle me-2"></i>
              Upload a CSV file with columns: Name, Marketplace, Seller ID, API Key
            </div>
            <div className="mb-3">
              <button className="btn btn-outline-primary btn-sm mb-2" onClick={downloadTemplate}>
                <i className="bi bi-download me-1"></i>Download Template
              </button>
            </div>
            <div className="mb-3">
              <label className="form-label">Paste CSV Data</label>
              <textarea 
                className="form-control" 
                rows="8"
                value={csvData}
                onChange={(e) => setCsvData(e.target.value)}
                placeholder="Seller Name,Marketplace,Seller ID,API Key&#10;TechGear Pro,amazon.in,A1B2C3D4E5F6,oct_xxx123&#10;HomeEssentials,amazon.com,A2B3C4D5E6F7,oct_xxx456"
              ></textarea>
            </div>
          </div>
          <div className="modal-footer">
            <button type="button" className="btn btn-secondary" onClick={onClose}>Cancel</button>
            <button className="btn btn-primary" onClick={handleImport} disabled={!csvData.trim()}>
              <i className="bi bi-file-earmark-check me-2"></i>Import
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

// Seller ASINs Modal Component
const SellerAsinsModal = ({ seller, asins, onClose, onAddAsin, onDeleteAsin }) => {
  const [showAddAsinModal, setShowAddAsinModal] = useState(false);
  const [newAsin, setNewAsin] = useState('');

  const handleAddAsin = () => {
    if (newAsin.trim()) {
      onAddAsin({ asinCode: newAsin.trim().toUpperCase() });
      setNewAsin('');
      setShowAddAsinModal(false);
    }
  };

  return (
    <div className="modal show d-block" style={{ backgroundColor: 'rgba(0,0,0,0.5)' }}>
      <div className="modal-dialog modal-lg">
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title">
              <i className="bi bi-box-seam me-2"></i>
              ASINs - {seller.name}
            </h5>
            <button type="button" className="btn-close" onClick={onClose}></button>
          </div>
          <div className="modal-body">
            <div className="d-flex justify-content-between align-items-center mb-3">
              <span className="text-muted">{asins.length} ASINs</span>
              <button className="btn btn-primary btn-sm" onClick={() => setShowAddAsinModal(true)}>
                <i className="bi bi-plus-lg me-1"></i>Add ASIN
              </button>
            </div>
            <div className="table-responsive">
              <table className="table data-table mb-0">
                <thead>
                  <tr>
                    <th>ASIN</th>
                    <th>Title</th>
                    <th>Brand</th>
                    <th>Price</th>
                    <th>Rank</th>
                    <th>Status</th>
                    <th>Actions</th>
                  </tr>
                </thead>
                <tbody>
                  {asins.length === 0 ? (
                    <tr>
                      <td colSpan="7" className="text-center text-muted py-4">
                        <i className="bi bi-inbox d-block mb-2" style={{ fontSize: '24px' }}></i>
                        No ASINs added yet. Click "Add ASIN" to add one.
                      </td>
                    </tr>
                  ) : (
                    asins.map(asin => (
                      <tr key={asin._id}>
                        <td className="fw-medium">{asin.asinCode}</td>
                        <td style={{ maxWidth: '200px', overflow: 'hidden', textOverflow: 'ellipsis', whiteSpace: 'nowrap' }}>
                          {asin.title || '-'}
                        </td>
                        <td>{asin.brand || '-'}</td>
                        <td>â‚¹{asin.currentPrice?.toFixed(2) || '0.00'}</td>
                        <td>#{asin.currentRank?.toLocaleString() || '-'}</td>
                        <td>
                          <span className={`badge ${asin.status === 'Active' ? 'badge-success' : 'badge-secondary'}`}>
                            {asin.status}
                          </span>
                        </td>
                        <td>
                          <button 
                            className="btn btn-sm btn-outline-danger"
                            onClick={() => onDeleteAsin(asin._id)}
                            title="Delete"
                          >
                            <i className="bi bi-trash"></i>
                          </button>
                        </td>
                      </tr>
                    ))
                  )}
                </tbody>
              </table>
            </div>
          </div>
          <div className="modal-footer">
            <button className="btn btn-secondary" onClick={onClose}>Close</button>
          </div>
        </div>
      </div>

      {/* Add ASIN Modal */}
      {showAddAsinModal && (
        <div className="modal show d-block" style={{ backgroundColor: 'rgba(0,0,0,0.3)' }}>
          <div className="modal-dialog">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title"><i className="bi bi-plus-circle me-2"></i>Add ASIN</h5>
                <button type="button" className="btn-close" onClick={() => setShowAddAsinModal(false)}></button>
              </div>
              <div className="modal-body">
                <div className="mb-3">
                  <label className="form-label">ASIN Code *</label>
                  <input 
                    type="text" 
                    className="form-control" 
                    value={newAsin}
                    onChange={(e) => setNewAsin(e.target.value.toUpperCase())}
                    placeholder="B07XYZ123"
                    maxLength={10}
                  />
                  <div className="form-text">Enter the 10-character Amazon ASIN</div>
                </div>
              </div>
              <div className="modal-footer">
                <button className="btn btn-secondary" onClick={() => setShowAddAsinModal(false)}>Cancel</button>
                <button className="btn btn-primary" onClick={handleAddAsin} disabled={!newAsin.trim()}>
                  <i className="bi bi-plus-lg me-2"></i>Add ASIN
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default SellersPage;
